import React, { Component } from 'react'
import { BASE_URL, CLOUDPATH, CLOUDPATH_TAGGSHOP, HOST_URL } from '../../../actions/api';
import { Link } from 'react-router-dom';
import { Php_Logout, logoutUser } from '../../../actions/loginActions';
import { error } from 'jquery';
import { isWebsiteChanged } from '../../../utils/checkForNavigation';
import withRouter from '../../routes/WithRouter';

 class userSiderBar extends Component {
    logOutAccount=(e)=>{  
        //   localStorage.removeItem("token") 
        //   localStorage.removeItem("loginOwnerId") 
        //   localStorage.removeItem("ownerId") 
        //   localStorage.removeItem("wallId") 
        logoutUser().then(res=> {
            localStorage.clear()
            Php_Logout()
            // window.location.replace(`${BASE_URL}`)
        }).catch(error=> {
            console.error("catch----------------------error",error)
            
        })
      }

      closeSidebar = () => {
        const elem = document.getElementById('kt_quick_user_close')
        if(elem) elem.click()
      }

    render() {
        const {authorized, navigate} = this.props  
        const user = authorized?.userData?.user_data.user 
        const profileImg = user?.picture
        const user_name = user?.name
        return (
            <div id="kt_quick_user" className="offcanvas offcanvas-end p-5 profile_sidebar" tabIndex="-1" aria-labelledby="offcanvasRightLabel" aria-modal="true" role="dialog" style={{ visibility: 'visible' }}>
                 
                 {JSON.parse(localStorage.getItem('isAdmin')) ?
                <a onClick={() => localStorage.clear()} href={`${BASE_URL}/v2/users/userLoginBackAdmin`} className="whitelabelanchor btn btn-sm btn-secondary font-weight-bolder py-2 px-5 d-block mb-6">Back to Admin</a>
                 : null}

                <div className="offcanvas-header d-flex align-items-center justify-content-between pb-5">
                    <h3 className="font-weight-bold m-0"> Profile </h3>
                    <button type="button" className="btn btn-xs btn-icon btn-light btn-hover-primary" id="kt_quick_user_close" data-bs-dismiss="offcanvas" aria-label="Close">
                        <i className="ki ki-close icon-xs text-muted"></i>
                    </button>
                </div>

                <div className="offcanvas-content pr-5 mr-n5">
                    <div className="d-flex align-items-start mt-5">
                        <div className="symbol symbol-100 mr-5 symbol-light-warning mt-2 userProfile_image">
                            {profileImg ? <div className="symbol-label" style={{ backgroundImage: `url('${profileImg}')` }}></div>
                            :
                            <span className="symbol-label font-size-h1 font-weight-bold text-uppercase">{user_name ? user_name[0]?.toUpperCase() : ""}</span>}
                            <span className="premium-user-sign user-sign-dropdown-sign">
                                <svg xmlns="http://www.w3.org/2000/svg" xlink="http://www.w3.org/1999/xlink" width="20" height="20" viewBox="0 0 20 20">
                                    <defs>
                                        <linearGradient id="linear-gradient" x1="0.5" y1="1.077" x2="0.5" gradientUnits="objectBoundingBox">
                                            <stop offset="0" stopColor="#fd8800"></stop>
                                            <stop offset="1" stopColor="#fec400"></stop>
                                        </linearGradient>
                                        <linearGradient id="linear-gradient-2" x1="0.5" y1="1" x2="0.5" gradientUnits="objectBoundingBox">
                                            <stop offset="0" stopColor="#ffe59a"></stop>
                                            <stop offset="1" stopColor="#ffffd5"></stop>
                                        </linearGradient>
                                    </defs>
                                    <g id="Group_4832" data-name="Group 4832" transform="translate(6313.593 292.593)">
                                        <g id="Group_4635" data-name="Group 4635" transform="translate(-6313.593 -292.593)">
                                            <g id="Group_4634" data-name="Group 4634" transform="translate(0 0)">
                                                <circle id="Ellipse_183" data-name="Ellipse 183" cx="10" cy="10" r="10" fill="url(#linear-gradient)"></circle>
                                            </g>
                                        </g>
                                        <g id="star" transform="translate(-6310.039 -289.039)">
                                            <path id="Path_4779" data-name="Path 4779" d="M7.018.15,8.581,3.631l3.8.391a.634.634,0,0,1,.355,1.1L9.9,7.716l.781,3.73a.62.62,0,0,1-.924.675L6.414,10.2l-3.3,1.918a.62.62,0,0,1-.924-.675l.781-3.73L.127,5.123a.634.634,0,0,1,.355-1.1l3.8-.391L5.846.15A.63.63,0,0,1,7.018.15Z" transform="translate(0.089 0.25)" fillRule="evenodd" fill="url(#linear-gradient-2)"></path>
                                        </g>
                                    </g>
                                </svg>
                            </span>
                        </div>
                        <div className="d-flex flex-column w-100 userProfile_detail">
                            <a onClick={() =>{ this.closeSidebar() ; isWebsiteChanged(() => navigate('/profile'))}} className="font-weight-bold text-dark-75 text-hover-primary text-break userProfile_title"  style={{cursor:'pointer'}}>{user?.name } </a>
                            <div className="navi mt-1">
                                <a className="navi-item ">
                                    <span className="navi-link p-0 pb-2 align-items-center userEmail_box">
                                        <span className="navi-icon mr-1">
                                            <span className="svg-icon svg-icon-primary">
                                                <svg xmlns="http://www.w3.org/2000/svg" xlink="http://www.w3.org/1999/xlink" width="24px" height="24px" viewBox="0 0 24 24" version="1.1">
                                                    <g stroke="none" strokeWidth="1" fill="none" fillRule="evenodd">
                                                        <rect x="0" y="0" width="24" height="24"></rect>
                                                        <path d="M21,12.0829584 C20.6747915,12.0283988 20.3407122,12 20,12 C16.6862915,12 14,14.6862915 14,18 C14,18.3407122 14.0283988,18.6747915 14.0829584,19 L5,19 C3.8954305,19 3,18.1045695 3,17 L3,8 C3,6.8954305 3.8954305,6 5,6 L19,6 C20.1045695,6 21,6.8954305 21,8 L21,12.0829584 Z M18.1444251,7.83964668 L12,11.1481833 L5.85557487,7.83964668 C5.4908718,7.6432681 5.03602525,7.77972206 4.83964668,8.14442513 C4.6432681,8.5091282 4.77972206,8.96397475 5.14442513,9.16035332 L11.6444251,12.6603533 C11.8664074,12.7798822 12.1335926,12.7798822 12.3555749,12.6603533 L18.8555749,9.16035332 C19.2202779,8.96397475 19.3567319,8.5091282 19.1603533,8.14442513 C18.9639747,7.77972206 18.5091282,7.6432681 18.1444251,7.83964668 Z" fill="#000000"></path>
                                                        <circle fill="#000000" opacity="0.3" cx="19.5" cy="17.5" r="2.5"></circle>
                                                    </g>
                                                </svg>
                                            </span>
                                        </span>
                                        <span className="navi-text text-muted text-hover-primary user_email_Id" onClick={()=>navigator.clipboard.writeText(user?.emailId)}>{user?.emailId}</span>
                                    </span>
                                </a>
                                <a onClick={() => this.logOutAccount()} className="whitelabelanchor btn btn-sm btn-secondary font-weight-bolder py-2 px-5 signout">Sign Out</a> </div>
                            </div>
                        </div>

                    <div className="separator separator-dashed mt-8 mb-5"></div>

                    <div className="navi navi-spacer-x-0 p-0">

                        {/* <Link to={"/profile"} className="navi-item" onClick={this.closeSidebar}> */}
                        <div onClick={() =>{ this.closeSidebar() ; isWebsiteChanged(() => navigate('/profile'))}} className="navi-item" style={{cursor:'pointer'}}>
                            <div className="navi-link">
                                <div className="symbol symbol-40 bg-light mr-3">
                                    <div className="symbol-label">
                                        <span className="svg-icon svg-icon-md svg-icon-success">
                                            <svg xmlns="http://www.w3.org/2000/svg" xlink="http://www.w3.org/1999/xlink" width="24px" height="24px" viewBox="0 0 24 24" version="1.1">
                                                <g stroke="none" strokeWidth="1" fill="none" fillRule="evenodd">
                                                    <polygon points="0 0 24 0 24 24 0 24"></polygon>
                                                    <path d="M12,11 C9.790861,11 8,9.209139 8,7 C8,4.790861 9.790861,3 12,3 C14.209139,3 16,4.790861 16,7 C16,9.209139 14.209139,11 12,11 Z" fill="#000000" fillRule="nonzero" opacity="0.3"></path>
                                                    <path d="M3.00065168,20.1992055 C3.38825852,15.4265159 7.26191235,13 11.9833413,13 C16.7712164,13 20.7048837,15.2931929 20.9979143,20.2 C21.0095879,20.3954741 20.9979143,21 20.2466999,21 C16.541124,21 11.0347247,21 3.72750223,21 C3.47671215,21 2.97953825,20.45918 3.00065168,20.1992055 Z" fill="#000000" fillRule="nonzero"></path>
                                                </g>
                                            </svg>
                                        </span>
                                    </div>
                                </div>
                                <div className="navi-text myProfile">
                                    <div className="font-weight-bold myProfile_title">
                                        My Profile
                                    </div>
                                    <div className="text-muted myProfile_desc">
                                        View/Edit your personal information
                                    </div>
                                </div>
                                </div>
                                </div>
                        {/* </Link> */}


                        {/* <Link to={"/notifications"} className="navi-item" onClick={this.closeSidebar}> */}
                        <div onClick={() =>{ this.closeSidebar() ; isWebsiteChanged(() => navigate('/notifications'))}} className="navi-item"  style={{cursor:'pointer'}}>
                            <div className="navi-link myNotification">
                                <div className="symbol symbol-40 bg-light mr-3">
                                    <div className="symbol-label">
                                        <span className="svg-icon svg-icon-md svg-icon-warning">
                                            <svg xmlns="http://www.w3.org/2000/svg" xlink="http://www.w3.org/1999/xlink" width="24px" height="24px" viewBox="0 0 24 24" version="1.1">
                                                <g stroke="none" strokeWidth="1" fill="none" fillRule="evenodd">
                                                    <rect x="0" y="0" width="24" height="24"></rect>
                                                    <path d="M22,15 L22,19 C22,20.1045695 21.1045695,21 20,21 L4,21 C2.8954305,21 2,20.1045695 2,19 L2,15 L6.27924078,15 L6.82339262,16.6324555 C7.09562072,17.4491398 7.8598984,18 8.72075922,18 L15.381966,18 C16.1395101,18 16.8320364,17.5719952 17.1708204,16.8944272 L18.118034,15 L22,15 Z" fill="#000000"></path>
                                                    <path d="M2.5625,13 L5.92654389,7.01947752 C6.2807805,6.38972356 6.94714834,6 7.66969497,6 L16.330305,6 C17.0528517,6 17.7192195,6.38972356 18.0734561,7.01947752 L21.4375,13 L18.118034,13 C17.3604899,13 16.6679636,13.4280048 16.3291796,14.1055728 L15.381966,16 L8.72075922,16 L8.17660738,14.3675445 C7.90437928,13.5508602 7.1401016,13 6.27924078,13 L2.5625,13 Z" fill="#000000" opacity="0.3"></path>
                                                </g>
                                            </svg>
                                        </span>
                                    </div>
                                </div>
                                <div className="navi-text">
                                    <div className="font-weight-bold myNotification_title">
                                        My Notifications
                                    </div>
                                    <div className="text-muted myNotification_desc">
                                        All Notifications
                                    </div>
                                </div>
                            </div>
                            </div>
                        {/* </Link> */}


                        {/* <Link to={"/transactions"} className="navi-item myAccount" onClick={this.closeSidebar}> */}
                        <div onClick={() =>{ this.closeSidebar() ; isWebsiteChanged(() => navigate('/transactions'))}} className="navi-item"  style={{cursor:'pointer'}} >
                            <div className="navi-link">
                                <div className="symbol symbol-40 bg-light mr-3">
                                    <div className="symbol-label">
                                        <span className="svg-icon svg-icon-md svg-icon-danger">
                                            <svg xmlns="http://www.w3.org/2000/svg" xlink="http://www.w3.org/1999/xlink" width="24px" height="24px" viewBox="0 0 24 24" version="1.1">
                                                <g stroke="none" strokeWidth="1" fill="none" fillRule="evenodd">
                                                    <rect x="0" y="0" width="24" height="24"></rect>
                                                    <path d="M4,4 L11.6314229,2.5691082 C11.8750185,2.52343403 12.1249815,2.52343403 12.3685771,2.5691082 L20,4 L20,13.2830094 C20,16.2173861 18.4883464,18.9447835 16,20.5 L12.5299989,22.6687507 C12.2057287,22.8714196 11.7942713,22.8714196 11.4700011,22.6687507 L8,20.5 C5.51165358,18.9447835 4,16.2173861 4,13.2830094 L4,4 Z" fill="#000000" opacity="0.3"></path>
                                                    <path d="M12,11 C10.8954305,11 10,10.1045695 10,9 C10,7.8954305 10.8954305,7 12,7 C13.1045695,7 14,7.8954305 14,9 C14,10.1045695 13.1045695,11 12,11 Z" fill="#000000" opacity="0.3"></path>
                                                    <path d="M7.00036205,16.4995035 C7.21569918,13.5165724 9.36772908,12 11.9907452,12 C14.6506758,12 16.8360465,13.4332455 16.9988413,16.5 C17.0053266,16.6221713 16.9988413,17 16.5815,17 C14.5228466,17 11.463736,17 7.4041679,17 C7.26484009,17 6.98863236,16.6619875 7.00036205,16.4995035 Z" fill="#000000" opacity="0.3"></path>
                                                </g>
                                            </svg>
                                        </span>
                                    </div>
                                </div>
                                <div className="navi-text">
                                    <div className="font-weight-bold myAccount_title">
                                        My Account
                                    </div>
                                    <div className="text-muted myAccount_desc">
                                        Account info, Plans and more
                                    </div>
                                </div>
                            </div>
                        </div>
                        {/* </Link> */}

                    </div>
                    <div className="separator separator-dashed my-7"></div>
                </div>
            </div>
        )
    }
}

export default withRouter(userSiderBar)